<script>
import Nav from "@/components/Nav.vue";
import { loadResource, loadStat } from "../../service/database";
import { __iconPath } from "@jx3box/jx3box-common/js/jx3box.json";
import User from "@jx3box/jx3box-common/js/user";
import {school} from '@jx3box/jx3box-data/data/xf/school.json'
export default {
    name: "Database",
    props: [],
    data: function() {
        return {
            type: "skill",
            query: "",
            npc_map: "",
            npc_level: "",
            strict: false,
            level: "",
            school: "",

            skill: [],
            advskill: [],
            buff: [],
            advbuff: [],
            npc: [],
            item: [],

            stat: {
                skill: 0,
                buff: 0,
                item: 0,
                npc: 0,
            },
            done: false,
            loading: false,

            isSuper: false,
            schools : school
        };
    },
    computed: {
        isBlank: function() {
            return !this.query && !this[this.type]["length"];
        },
    },
    methods: {
        getData: function(type) {
            if (!this.query) return;

            this.loading = true;
            this.done = false;
            let query = this.query;

            let params = {
                strict: ~~this.strict,
            };
            if (this.type == "npc") {
                if (this.npc_map) params.map = this.npc_map;
                if (this.npc_level) params.level = this.npc_level;
            }
            if (this.type == "skill" || this.type == "buff") {
                if (this.level) params.level = this.level;
            }
            if (this.type == "skill") {
                if (this.school || this.school == 0) params.school = this.school;
            }

            if (isNaN(query)) {
                loadResource(type, "name", query, params)
                    .then((data) => {
                        this[type] = this.transformData(data);
                    })
                    .finally(() => {
                        this.done = true;
                        this.loading = false;
                    });
            } else {
                query = parseInt(query);
                loadResource(type, "id", query, params)
                    .then((data) => {
                        this[type] = this.transformData(data);
                    })
                    .finally(() => {
                        this.done = true;
                        this.loading = false;
                    });
            }
        },
        search: function() {
            this.getData(this.type);
        },
        onCopy: function(val) {
            this.$notify({
                title: "复制成功",
                message: "复制内容 : " + val.text,
                type: "success",
            });
        },
        onError: function() {
            this.$notify.error({
                title: "复制失败",
                message: "请手动复制",
            });
        },
        toggleProps: function(o) {
            o.isopen = !o.isopen;
        },
        transformData: function(data) {
            data.forEach((item) => {
                item.isopen = false;
            });
            return data;
        },
    },
    filters: {
        filterRaw: function(str) {
            return str && str.replace(/\\n/g, "\n");
        },
        iconURL: function(id) {
            return __iconPath + "icon/" + id + ".png";
        },
    },
    created: function() {
        loadStat().then((data) => {
            this.stat = data;
        });
        this.isSuper = User.getInfo().group > 8;
    },
    mounted: function() {},
    components: {
        Nav,
    },
};
</script>

